.cards{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.cards .heading_box{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 20px 30px;
    margin-bottom: 40px;
    
}
.cards .heading_box .see_more{
    width: auto;
    height: auto;
    color: rgb(22, 165, 22);
    font-size: 18px;
}
.cards .heading_box span{
    font-size: 20px;
    font-weight: lighter;
    text-transform: uppercase;
    color: black;
}
.cards .heading_box a{
    font-size: 20px;
    text-decoration: none;
    text-transform: uppercase;
}
.cards .cards_box{
    width: 100%;
    height: auto;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    padding: 0px 10px;
    
}
.cards .cards_box .card_box{
    width: 320px;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
       
}

@keyframes arrow_ani {
    0%{
        transform: translateX(0px);
    }
    80%{
        transform: translateX(0px);
    }
    91%{
        transform: translateX(15px);

    }
    100%{
        transform: translateX(0px);
    }
}

.arrow_ani{
    animation: arrow_ani 2s infinite;
}

@media (max-width:550px) {
    .cards .heading_box span{
    font-size: 18px;
    text-transform: uppercase;
    
}
.cards .heading_box a{
    font-size: 18px;
}

}