.foods{
    width: 100%;
    height: auto;
    display: flex;
    /* background: url(https://cdn.wallpapersafari.com/62/71/Cij1ya.jpg); */
    background-repeat: no-repeat;
    flex-direction: column;
    background-size: 300px 200px;
    background-position: top left;
}
.foods .foods_title_box{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.foods .foods_title_box .food_title_chield_2{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.foods .foods_title_box .food_title_chield_1{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 200px;
    height: auto;
}
.foods .foods_title_box .food_title_chield_1 img{
    
    width: 100%;
    height: auto;
}
.foods .foods_title_box .food_title_chield_3{
    width: 200px;
    height: auto;
}
.foods .foods_title_box .food_title_chield_3 img{
    
    width: 90%;
    height: auto;
    margin-top: 20px;
}
.foods .foods_item_box{
    width: 100%;
    height: auto;
    padding: 0px 30px;
}
.foods .foods_item_box .food_item{
    width: 100%;
    height: 78vh;
    /* color: rgba(0, 0, 0, 0.369); */
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0px 20px 0px 20px;
    background-blend-mode: darken;
    /* background: url('https://cdn.wallpapersafari.com/1/6/SmKUu7.jpg'); */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}
.foods .foods_item_box .food_item .img_box{
    width: 45% !important;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.foods .foods_item_box .food_item .text_box{
    width: 55%;
    
}
.foods .foods_item_box .food_item .img_box img{
    width: 70%;
}
.foods .foods_item_box .food_item .text_box h3{
    font-size: 40px;
    color: white;
}
.foods .foods_item_box .food_item .text_box p{
    font-size: 20px;
    padding: 20px 0px;
    color: white;
}
.foods .foods_item_box .food_item .text_box a{
    font-size: 20px;
    text-decoration: none;
    padding: 7px 15px;
    border-radius: 5px;
    color: black;
}



@media (max-width:950px) {
    .foods .foods_item_box .food_item .img_box {
    width: 40%;
}
    .foods .foods_item_box .food_item .text_box {
    width: 60%;
}
.foods .foods_item_box .food_item .img_box img{
    width: 90%;
}
  .foods .foods_item_box .food_item .text_box p{
    padding: 0px 0px;
}
}
@media (max-width:770px){
      .foods .foods_item_box .food_item{
        height: 90vh;
            padding: 60px 0px 60px 0px;
        
    }
}
@media (max-width:770px) {

    .foods .foods_item_box .food_item{
        flex-direction: column !important;
        height: 90vh;
            padding: 30px 0px 30px 0px;
        
    }
  
    .foods .foods_item_box .food_item .img_box{
    width: 100% !important;
    
}
.foods .foods_item_box .food_item .text_box{
    width: 55%;
    
}
.foods .foods_item_box .food_item .img_box img{
    max-width: 350px;
    width: 90%;
}

.foods .foods_item_box .food_item .text_box{
    width: 90%;
    margin: auto;
    text-align: center;
    
}
.foods .foods_item_box .food_item .text_box h3{
    text-align: center;
    font-size: 35px;
}
.foods .foods_item_box .food_item .text_box p{
    font-size: 18px;
    padding: 20px 0px 0px 0px;
    text-align: center;
}
.foods .foods_item_box{
    width: 100%;
    height: auto;
    padding: 0px 10px;
}

}
.c_bg_color{
    background-color: #20e062 !important;
    color: white !important;
}