.footer {
  width: 100%;
  height: 80vh;
  margin: 0%;
  background-image: url(../../images/crop.png);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-top: 300px;
  padding-left: 100px;
}

.footer .address_box{
  
}
.footer .address_box .location{

}
.footer .address_box .icons{
  display: flex;
  text-align: start;
  justify-content: start;
  align-items: center;
}
.footer .address_box .icons span{
  width: 40px;
  height: 40px;
  border-radius: 50px;
  background-color: none;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.4s ease-in-out;

}
.footer .address_box .icons span:hover{
  background-color: white;

}
.footer .address_box .icons span:nth-child(1):hover{
  color: rgb(255, 6, 6);
}
.footer .address_box .icons span:nth-child(2):hover{
  color: rgb(6, 14, 255);
}
.footer .address_box .icons span:nth-child(3):hover{
  color: rgb(255, 6, 110);
}
.footer .address_box .icons span:nth-child(4):hover{
  color: rgb(0, 255, 255);
}


.footer .address_box .icons span>*{
  font-size: 26px !important;

}
.footer .address_box .contect_us_box{
   display: block;
  margin: 20px 0px;
  color: white;
}
.footer .address_box .location h3,span{ 
  display: block;
  margin: 20px 0px;
  color: white;

}
.footer .address_box h3, .footer .address_box span {
  font-size: 15px;
}

.footer .btn_box{
  height: 150px;
  display: grid;
  place-items: center;
 
}


@media (max-width:767px) {
  .footer {
  width: 100%;
  height: 110% ;
  margin: 0%;
  background-image: url(../../images/crop.png);
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: start;
  align-items: center;
  padding-top: 300px;
  padding-left: 100px;
  padding: 0px 30px;
}
.footer .address_box .icons{
  justify-content: center;

}
}