@keyframes textAnimation_c {
    0%{
        transform: translate(0px,1100px);
        
    }
    
    100%{
        transform: translate(0px,0px);
    }
}
@keyframes textAnimation_h {
    0%{
        transform: translate(0px,-1100px);
        
    }
    
    100%{
        transform: translate(0px,0px);
    }
}


#animation_heading{
    animation: textAnimation_h  1s ease-in 1;
}
#animation_content{
    /* animation: textAnimation_c  0.5s ease-in 1; */
    animation: textAnimation_c 1s ease-out;
    
}

@media (min-width:2500px) {
    .Home{
        height: 60% !important;
    }
}



.Home_page{
    width: 100%;
}
.Home{
    width: 100%;
    height: 95vh;
    position: relative;
    padding: 0px 50px;
    background-image: url(../../images/home_bg_3.png);
    background-size: cover;
    background-repeat: no-repeat;
}

.Home .nav_bar{
    position: absolute;
    top: 0%;
    left: 0%;
    width: 100%;
}
.Home .home_content {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.Home .home_content .home_inner_img {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content:  center;
    align-items: center;
    padding-top: 100px;
}
.Home .home_content .home_inner_content {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content:  center;
    align-items: flex-start;
    flex-direction: column;
}
.Home .home_content .home_inner_content h2{
    font-size: 50px;
    text-transform: uppercase;
}
.Home .home_content .home_inner_content p{
    font-size: 20px;
}

.Home .home_content .home_inner_content a{
    color: white;
}
@keyframes animated-scale {
    0%{
        transform: scale(1);
    }
    50%{
        transform: scale(0.98);
    }
    100%{
        transform: scale(1);
    }
}
@keyframes side {
    0%{
        transform: translateX(500px)
    }
    100%{
        transform: translateX(0px)
    }
}

.Home .home_content .mango{
    animation: animated-scale 3s  infinite,side 1s  1;
    
}




/* --------------------------------------------------------------------------------------------- */

@media (max-width:1050px) {
    .Home .home_content .home_inner_img {
    width: 50%;
    height: 100%;

}
    .Home .home_content .home_inner_img .mango {
       width: 90% !important;
       height: auto;
}


.Home .home_content .home_inner_content h2{
    font-size: 42px;
    animation: textAnimation 1s linear infinite;
    transition: all 0.5s ease-in;
}
.Home .home_content .home_inner_content p{
    font-size: 20px;
    /* animation: textAnimation 1s infinite; */
    animation: animated-scale 3s  infinite,side 1s  1;

}


}

@media (max-width:950px) {
    .Home .home_content .home_inner_img {
    width: 50%;
    height: 100%;

}
    .Home .home_content .home_inner_img .mango {
       width: 90% !important;
       height: auto;
}


.Home .home_content .home_inner_content h2{
    font-size: 35px;
}
.Home .home_content .home_inner_content p{
    font-size: 18px;
}

}

@media (max-width:767px) {
    .Home{
        /* height: 130vh; */
        height: 80vh;
    }
    .Home .home_content  {
    flex-direction: column;
    justify-content: flex-end !important;

}
    .Home .home_content .home_inner_content {
       /* padding-top: 100px; */
       width: 100%;
}
    .Home .home_content .home_inner_img {
       padding: 0px;
       width: 100%;
       display: none;
}
    .Home .home_content .home_inner_img .mango {
       width: 90% !important;
       height: auto;
}


.Home .home_content .home_inner_content h2{
    font-size: 35px;
}
.Home .home_content .home_inner_content p{
    font-size: 18px;
}

}