
.card{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0px;
}

.card .like_box{
    width: 100%;
    padding: 0px 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.card .like_box i{
    padding: 5px;
    border-radius: 5px;
    color: rgb(255, 0, 225);
    background-color:rgba(222, 12, 177, 0.11) ;
    cursor: pointer;
}
.card .like_box h4{
    font-size: 18px;
    padding: 4px 6px !important ;
    border-radius: 5px;
        background-color:rgba(12, 222, 61, 0.11) ;

}
.card .like_box h4{
    padding: 10px;
      background-color:#20e06339;
}
.card h6{
    font-weight: normal;
    font-size: 22px;
}
.card p{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}
.card p span:first-child{
    color: #20e062;
}
.card .button{
    border-radius: 10px !important;
    text-decoration: none;
    color: #20e062;
    padding: 10px 20px 6px 20px;
    background-color:#20e06339;
    font-size: 14px !important;
   
}
.card .button h5{
    display: inline-block;
    
}
.card img{
    width: 100%;
    height: auto;
    max-width: 150px;
    max-height: auto;
}