.chips_box{
    width: 100%;
    height: 70vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0px;
    padding: 0px 30px;
    margin-top: 50px;
    background: url('https://images7.alphacoders.com/402/thumb-1920-402852.jpg'),rgba(5, 5, 5, 0.281);
    background-blend-mode: darken;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    overflow: hidden;

}
.chips_box .text_box{
    width: 55%;
    height: auto;
        color: white;  
}
.chips_box .img_box{
    width: 45%;
    height: auto;
    padding: 30px;
    text-align: center;

}
@keyframes img_animation {
    0%{
        transform: translateY(0px);
        
    }
    
    50%{
        transform: translateY(-20px);
    }
    100%{
        transform: translateY(0px);
    }
}
.chips_box .img_box img{
    width: 75%;
    height: auto;
    animation: img_animation 4s linear infinite;
}
@keyframes ani_p {
    0%{
        transform: translate(-5000px,0px);
        
    }
    
    100%{
        transform: translate(0px,0px);
    }
}
.chips_box .text_box h2{
    font-size: 40px;
}
.chips_box .text_box p{
    margin-top: 30px;
    font-size: 18px;
    animation: ani_p 1s ease-in;
    line-height: 40px;

}


@media (max-width:1050px) {
    .chips_box .img_box{
    width: 50%;
    height: auto;
    padding: 0px;
    text-align: center;
}
.chips_box .img_box img{
    width: 75%;
    height: auto;
}
.chips_box .text_box h2{
    font-size: 38px;
}
.chips_box .text_box p{
    font-size: 18px;
}
}
@media (max-width:900px) {
    .chips_box .img_box{
    width: 50%;
    height: auto;
    padding: 0px;
    text-align: center;
}
.chips_box .img_box img{
    width: 70%;
    height: auto;
}
.chips_box .text_box h2{
    font-size: 35px;
}
.chips_box .text_box p{
    font-size: 18px;
}
}
@media (max-width:830px) {
    .chips_box{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
   flex-direction: column;
    padding: 20px 30px 0px 30px;
         background-position: center;

}
.chips_box .text_box{
    width: 100%;
    height: auto;
    order: 2;
    padding: 10px 30px;
}
    .chips_box .img_box{
    width: 60%;
    min-width: 200px;
    order: 1;
}
.chips_box .img_box img{
    width: 70%;
    height: auto;
}
.chips_box .text_box h2{
    font-size: 35px;
}
.chips_box .text_box p{
    font-size: 18px;
}
}
@media (max-width:530px) {
    .chips_box{
    padding: 0px 0px !important;

    }

    .chips_box .text_box{
    width: 100%;
    height: auto;
    order: 2;
    padding: 10px 0px;
    
}

.chips_box .text_box h2{
    text-align: center;
    font-size: 30px;
}
.chips_box .text_box p{
    text-align: center;
    font-size: 14px;
}
}
@media (max-width:350px) {
    .chips_box{
         padding: 20px 10px 0px 10px;
         background-position: center;
    }
    .chips_box .text_box{
    width: 100%;
    height: auto;
    order: 2;
    padding: 10px 0px;
}
}
