.f_btn button {
  position: relative;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  padding: 0;
  font-size: 16px;
  font-family: inherit;
  &.learn-more {
    width: 10rem;
    height: auto;
    .circle {
      @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
      position: relative;
      display: block;
      margin: 0;
      width: 2.4rem;
      height: 2.4rem;
      background: $white;
      border-radius: 1.625rem;
      color: #282936 !important;
      .icon {
        @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
        position: absolute;
        top: 0;
        
        bottom: 0;
        margin: auto;
        background: $white;
        color: black;
        &.arrow {
          @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
          left: 0.625rem;
          width: 15px;
          height: 0.125rem;
          
          background: none;
          &::before {
            position: absolute;
            content: '';
            top: -0.25rem;
            right: 0.0625rem;
            width: 0.625rem;
            height: 0.625rem;
            border-top: 0.125rem solid rgb(0, 0, 0);
            border-right: 0.125rem solid rgb(0, 0, 0);

            transform: rotate(45deg);
      color: #282936 !important;

          }
        }
      }
    }
    .button-text {
      @include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0.75rem 0;
      margin: 0 0 0 1.85rem;
      color: $white;
      font-weight: 700;
      line-height: 16px;
      text-align: center;
      text-transform: uppercase;
    }
  }
  &:hover {
    .circle {
      width: 100%;
      background-color: $black;
      .icon {
        color: #282936;
        &.arrow {
        background: $white;
        transform: translate(1rem, 0);
        color: $black !important;
        }
      }
    }
    .button-text {
      color: $white;
      &:hover{
        color: $black;
      }
    }
  }
}
button:hover .button-text{
  color: $white !important;
}
button:hover .arrow::before{
          border-top: 0.125rem solid rgb(255, 255, 255) !important;
            border-right: 0.125rem solid rgb(255, 255, 255) !important ;
}

@supports (display: grid) {

  
 .f_btn #container {
    grid-area: main;
    align-self: center;
    justify-self: center;
  }
}