
:root {
   --card-gradient: rgba(0, 0, 0, 0.8);
  --card-gradient: #5e9ad9, #e271ad;
   --card-gradient: tomato, orange;
  --card-blend-mode: overlay;
   --card-blend-mode: multiply;
}

.owl_card{
 background-color: #fff;
  border-radius: 0.5rem;
  box-shadow: 0.05rem 0.1rem 0.3rem -0.03rem rgba(0, 0, 0, 0.45);
  padding-bottom: 1rem;
  background-image: linear-gradient(
    var(--card-gradient),
    white max(9.5rem, 27vh)
  );
  overflow: hidden;
  height: 490;
  margin: auto;
}
.owl_card:hover,.owl_card:focus-within{
    --card-gradient: #24a9d5 max(8.5rem, 20vh);

}
 .owl_card>:last-child {
    margin-bottom: 0;
  }
.owl_card  img {
    border-radius: 0.5rem 0.5rem 0 0;
    width: 100%;
    object-fit: cover;
     height: max(9rem, 20vh);  
     /* height: 100px; */
    max-height: max(10rem, 30vh);
    aspect-ratio: 4/3;
    mix-blend-mode: var(--card-blend-mode);
     filter: grayscale(100);
  }
  .owl_card img ~*{
 margin-left: 1rem;
      margin-right: 1rem;
  }



.owl_card h3 {
  margin-top: 1rem;
  font-size: 1.25rem;
}

.owl_card a {
  color: inherit;
}

.card-wrapper {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(30ch, 1fr));
  grid-gap: 1.5rem;
  max-width: 100vw;
  width: 120ch;
  padding-left: 1rem;
  padding-right: 1rem;
}
